* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;
}

.App {
  /* text-align: center; */
  min-height: 100%;
  font-family: 'Open Sans', sans-serif;
}

.container {
  margin: auto;
  min-height: 100%;
  max-width: 1200px;
  padding: 5px 15px;
}

.card {
  display: flex;
  justify-content: flex-start;
}

.gj {
  width: 100%;
  max-width: 200px;
  padding:5px 5px 10px 5px;
}

.light {
  background-color: rgb(241, 241, 241);
}
.dark {
  background-color: rgb(85, 151, 173);
  color: white;
}

header {
  background-color: rgb(12, 127, 153);
  padding: 20px;
  color: white;
}
header h1 {
  text-align: center;
}

nav {
  margin: 0 5px 10px 5px;
  padding: 5px;
  background-color: rgb(12, 127, 153);
}
nav .container {
  display: flex;
  justify-content: flex-start;
  padding: 10px;
}
nav .button {
  margin: 10px;
}

.programs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
}
.program {
  display: flex;
  flex-direction: column;
  margin: 10px 5px;
  max-width: 200px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,0.2);
  text-decoration: none;
  color: rgb(40, 40, 40);
  background-color: rgb(250, 250, 250);
  transition: all 0.5s ease;
}
.program:hover {
  background-color: rgb(250, 250, 250);
  transform: scale(1.05);
}
.program-image img {
  width: 100%;
}
.program-details h3 {
  margin: 5px;
  font-size: 0.95em;
}
.program-footer {
  font-size: 0.8em;
  padding: 8px 2px;
  margin-top: auto;
  background-color: rgba(20, 20, 20, 0.05);
}

